import React from "react";
import './Categories.scss'
import Category from "./Category";
import { Link } from "react-router-dom";
import {moveAwayFromHomeAction} from "../../actions/navActions";
import {connect} from "react-redux";

class Categories extends React.Component {

    componentDidMount() {
        this.props.showBackButton(false);
    }

    componentWillUnmount() {
        this.props.showBackButton(true);
        console.log("skajh")
    }

    render() {
        return (
            <div className="categories">
                {
                    this.props.categories.map((category, index) => {
                        return (
                            <Link to={'/gallery/' + category.link} key={index}>
                                <Category title={category.title} image={category.image} />
                            </Link>
                        );
                    })
                }
            </div>
        );
    }
}

const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({
    showBackButton: (flag) => {
        dispatch(moveAwayFromHomeAction(flag))
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(Categories);
