const defaultState = {
    isOpen: false,
    displayBackButtonIfMobile: false
};

export default function (state = defaultState, action) {

    switch (action.type) {
        case "TOGGLE_NAV":
            state = {
                ...state,
                isOpen: !state.isOpen
            };
            break;
        case "ENABLE_BACK_BUTTON":
            state = {
                ...state,
                displayBackButtonIfMobile: action.payload
            };
            break;

        default:
            state = {
                ...state
            };
    }

    return state;
}
